/* office doesn't allow clipboard access so use the browser.
  User will be asked if accessing clipboard is okay
*/
export function pasteUnformatted() {
  navigator.clipboard.readText().then((clipText) => {
    Word.run(async (context) => {
      const selection = context.document.getSelection();
      selection.insertText(clipText, Word.InsertLocation.replace);
      await context.sync();
    });
  }).catch((err) => {
    console.error("Clipboard access failed:", err);
  });
}

/*
 Set the Style
*/

export async function setStyle(styleName) {

  console.log("setStyle " + styleName);

   try {
    await Word.run(async (context) => {
      const selection = context.document.getSelection();
      selection.style = styleName;
      await context.sync();
    });
  } catch (error) {

    // Todo: make a common helper
    console.log("Error applying style:", styleName, "Most likely style doesn't exist in document.");

    if (error instanceof OfficeExtension.Error) {
        console.error("Debug info:", {
      message: error.message,
      code: error.code,
      trace: error.traceMessages,
    });
  }


  }
}


/*
Office.JS doesn't have a "Clear Formatting" so have to manually reset each of the
properties we care about resetting to the Style.
*/
export async function clearFormatting() {
  try {
    await Word.run(async (context) => {
      const selection = context.document.getSelection();
      const font = selection.font;

      // Safely reset local formatting overrides
      font.bold = false;
      font.italic = false;
      font.underline = "None";
      font.color = "auto";
      font.highlightColor = "None";
      font.strikeThrough = false;
      font.subscript = false;
      font.superscript = false;

      await context.sync();
      console.log("Local formatting reset to style defaults.");
    });
  } catch (error) {
    console.error("Error resetting formatting:", error);
    if (error instanceof OfficeExtension.Error) {
      console.error("Details:", {
        message: error.message,
        code: error.code,
        trace: error.traceMessages,
      });
    }
  }
}


