var netwealth_CacheXml = null;
var ahrc_CacheXml = null;


export async function insertAutoText(autoText_identifier) {
  try {

    let  xmlDoc = null;

    // make sure xml is loaded
    if (!netwealth_CacheXml) {
      // Step 1: Fetch custom.xml from assets
      //const response = await fetch("./assets/netwealth_custom.xml");

           const response = await fetch("./assets/netwealth_custom.xml");

      if (!response.ok) {
        throw new Error(`Failed to fetch custom.xml: ${response.statusText}`);
      }

      const xmlText = await response.text();

      // Step 2: Parse XML and locate the snippet
      const parser = new DOMParser();
      xmlDoc = parser.parseFromString(xmlText, "application/xml");
      netwealth_CacheXml = xmlDoc;
    }

      // make sure xml is loaded
    if (!ahrc_CacheXml) {
      // Step 1: Fetch custom.xml from assets
      const response = await fetch("./assets/ahrc_custom.xml");
      if (!response.ok) {
        throw new Error(`Failed to fetch custom.xml: ${response.statusText}`);
      }

      const xmlText = await response.text();

      // Step 2: Parse XML and locate the snippet
      const parser = new DOMParser();
      xmlDoc = parser.parseFromString(xmlText, "application/xml");
      ahrc_CacheXml = xmlDoc;
    }

    //  hack we just check both xmls for prototypeing.
    let snippetNode = netwealth_CacheXml.querySelector(`Snippet[id='${autoText_identifier}']`);

    if (!snippetNode) {
      snippetNode = ahrc_CacheXml.querySelector(`Snippet[id='${autoText_identifier}']`);
      if (!snippetNode) {
      throw new Error(`Snippet with id '${autoText_identifier}' not found.`);
      }
    }

    const snippet = snippetNode.textContent;

    // Step 3: Insert OOXML into Word document
    await Word.run(async (context) => {
      const selection = context.document.getSelection();
      selection.insertOoxml(snippet, Word.InsertLocation.replace);
      await context.sync();
    });

  } catch (error) {
    console.error("Error inserting autotext:", error);
    if (error instanceof OfficeExtension.Error) {
      console.error("Details:", {
        message: error.message,
        code: error.code,
        trace: error.traceMessages,
      });
    }
  }
}

export async function insertAutoTextContentXml(autoText_identifier) {
  try {
        await Word.run(async (context) => {
          const parts = context.document.customXmlParts.getByNamespace("http://schemas.adu/addin/autotext");
          parts.load("items");
          await context.sync();

          if (parts.items.length === 0) {
            throw new Error("No matching CustomXmlParts found.");
          }

          const xmlPart = parts.items[0]; // Now confidently scoped
          const xml = xmlPart.getXml();
          await context.sync();

          const parser = new DOMParser();
          const xmlDoc = parser.parseFromString(xml.value, "text/xml");
          const snippetNode = xmlDoc.querySelector(`Snippet[id='${autoText_identifier}']`);

          if (!snippetNode) {
            throw new Error(`Snippet with id '${autoText_identifier}' not found.`);
          }

          const snippet = snippetNode.textContent;

          const selection = context.document.getSelection();
          selection.insertOoxml(snippet, Word.InsertLocation.replace);

          await context.sync();
        });

  } catch (error) {
    console.error("Error inserting autotext:", error);
    if (error instanceof OfficeExtension.Error) {
      console.error("Details:", {
        message: error.message,
        code: error.code,
        trace: error.traceMessages,
      });
    }
  }
}

/*
todo: test code for changing style font size
Office.js doesn't support changing the font size, 
Could looking into using OOXML direction
*/
export async function setCoverHeadingFontSize(size: number) {
  try {
    await Word.run(async (context) => {
      const props = context.document.properties.customProperties;
      const styles = context.document.getStyles();
      
      props.load("items");
      const coverHeadingStyle = styles.getByNameOrNullObject("Cover Heading");
      coverHeadingStyle.load("font");
      await context.sync();

      const getProp = (name: string) => {
        const prop = props.items.find(p => p.key === name);
        return prop ? prop.value : null;
      };

      const coverChosen = getProp("CoverChosen");
      const docType = getProp("DocumentType");

      // Todo: clean this up.
      const caption = "setCoverHeadingFontSize";     // Replace with actual value
      const docTypeLabel = ""; // Replace with actual value

      if (coverChosen === "1" || coverChosen === "2") {
        console.log(`${caption} ${docTypeLabel}: This option is not designed for the cover you've chosen.`);
        return;
      } else if (docType === "CustomNoCover") {
        console.log(`${caption} ${docTypeLabel}: This option is not designed for the custom layout you've chosen.`);
        return;
      } else if (docType === "Simple") {
        console.log(`${caption} ${docTypeLabel}: This option is not designed for the simple layout you've chosen.`);
        return;
      }

      if (coverHeadingStyle.isNullObject) {
        console.log("Style 'Cover Heading' not found.");
        return;
      }

      coverHeadingStyle.font.size = size;
      await context.sync();

      console.log(`Updated 'Cover Heading' font size to ${size}pt.`);
    });
  } catch (error) {
    console.error("Error during validation or style update:", error);
  }
}

