async function applyStyleToCellParagraphs(cells, styleName, context) {
  cells.forEach(cell => cell.load("body"));
  await context.sync();

  cells.forEach(cell => {
    const paragraphs = cell.body.paragraphs;
    paragraphs.load("items");
  });
  await context.sync();

  cells.forEach(cell => {
    cell.body.paragraphs.items.forEach(paragraph => {
      paragraph.style = styleName;
    });
  });
  await context.sync();
}


