async function insertImageIntoWord() {
  const input = document.createElement("input");
  input.type = "file";
  input.accept = "image/*";
  input.style.display = "none";

  input.onchange = function () {
    const file = input.files[0];
    if (!file) return;

    const reader = new FileReader();
    reader.onload = function () {
      const result = reader.result;

      // Ensure it's a data URL and extract base64
      const base64 = result.startsWith("data:image")
        ? result.substring(result.indexOf(",") + 1)
        : null;

      if (!base64) {
        console.error("Invalid image format or reader result.");
        return;
      }

      Word.run(async (context) => {
        const selection = context.document.getSelection();
        selection.insertInlinePictureFromBase64(base64, Word.InsertLocation.replace);
        await context.sync();

        // Style the paragraph at the current selection
        const paragraph = selection.paragraphs.getFirst();
        paragraph.load("style");
        await context.sync();

        // todo: VBA code set the style to "Picture" but that style doesn't exist. Not sure why not
        // causing error.
        paragraph.style = "Normal";
        await context.sync();
      });
    };

    reader.readAsDataURL(file); // Ensures result is a data URL
  };

  document.body.appendChild(input);
  input.click();
}

async function screenshotFormatGuide() {

   // make a new paragraph where selections paragraph ends
   await Word.run(async (context) => {
        const selection = context.document.getSelection();

        // Get the paragraph containing the selection
        const currentParagraph = selection.paragraphs.getFirst();
        currentParagraph.load("range");
        await context.sync();

        // Move cursor to the end of that paragraph
        const endOfParagraph = currentParagraph.getRange(Word.RangeLocation.end);

        // Insert a new paragraph after it
        const newParagraph = endOfParagraph.insertParagraph("", Word.InsertLocation.after);

        // Load the range of the new paragraph
      //  const newRange = newParagraph.getRange();
       // newRange.select(); // Move the selection to the new paragraph

        // Collapse the range to its end (insertion point only)
        // this keeps the inserted auto text from being selected when done.
        const collapsedRange = newParagraph.getRange(Word.RangeLocation.end);
        collapsedRange.select(); // Moves cursor without selecting content


        await context.sync();

        // Type your desired text
        //newParagraph.insertText("Your text goes here.", Word.InsertLocation.start);

        await context.sync();
      });

  //    .AttachedTemplate.AutoTextEntries("ScreenShotFormat").Insert Where:=Selection.Range, RichText:=True
      insertAutoText("ScreenShotFormat");

}


async function insertDividerPage() {
    await Word.run(async (context) => {
        const doc = context.document;
        const body = doc.body;

        // Hardcoded values
        const dividerNumber = "3";
        const dividerTitles = {
            DividerTitle1: "Executive Summary",
            DividerTitle2: "Financial Overview",
            DividerTitle3: "Risk Assessment"
        };
        const colorTheme = "Blue";

        const endRange = context.document.body.getRange("End");

        // Insert page break at end
        endRange.select();

        insertAutoText("DividerGrid");
        
        endRange.insertBreak(Word.BreakType.page, Word.InsertLocation.after);

        // Simulate AutoText: DividerGrid
      //  const dividerGridHtml = "<p><strong>[DividerGrid]</strong></p>";
     //   body.insertHtml(dividerGridHtml, Word.InsertLocation.end);

     //   body.insertBreak(Word.BreakType.page, Word.InsertLocation.end);

        await context.sync();

        // Insert divider titles at bookmarks
        for (let i = 1; i <= 3; i++) {
            const title = dividerTitles[`DividerTitle${i}`];
            const bookmarkName = `DividerTitle${i}`;
            const bookmarkRange = doc.getBookmarkRangeOrNullObject(bookmarkName);
            bookmarkRange.load("isNull"); //t todo: this seems incorrect.
            await context.sync();

            if (bookmarkRange) {
                bookmarkRange.select();
                bookmarkRange.insertText(title, Word.InsertLocation.replace);
            // according to office.js docs bookmark is remove when string replaced.    bookmarkRange.delete();
            }
        }

        await context.sync();

        // Insert logo based on color theme
        const logoBookmark = doc.getBookmarkRangeOrNullObject("DividerLogo");
        logoBookmark.load("isNull");
        await context.sync();

        if (logoBookmark) {
            logoBookmark.select();
            insertAutoText("Logo_BlueShade"); // todo: hardcoded hack

            // todo; review if logo bookamark was removed
        }

        await context.sync();
        
        // Insert divider number block
        const dividerNoBookmark = doc.getBookmarkRangeOrNullObject("DividerNo");
        dividerNoBookmark.load("isNull");
        await context.sync();

        if (dividerNoBookmark) {
            dividerNoBookmark.select();
            insertAutoText("DividerNo_6_BlueShade"); // todo: shouldn't be hardcoded

            await context.sync();

            // Update fields (e.g., TOC)
            const fields = doc.body.fields;
            fields.load("items");
            await context.sync();

            fields.items.forEach(field => field.updateResult());
        }

        await context.sync();
    });
}


