function isValidBookmarkName(name) {
  // Must start with a letter and contain only letters, numbers, or underscores
  return /^[A-Za-z][A-Za-z0-9_]*$/.test(name);
}

async function bookmarkExists(bookmarkName ) {
  if (!isValidBookmarkName(bookmarkName)) {
    console.warn(`Bookmark name "${bookmarkName}" is unsafe for Office.js`);
    return false;
  }

  let exists = false;

  try {
    await Word.run(async (ctx) => {
      const range = ctx.document.getBookmarkRangeOrNullObject(bookmarkName);
      range.load();
      await ctx.sync();

    //  exists = typeof range.text === "string";
      exists = true;
    });
  } catch (error) {
    console.warn(`Bookmark lookup failed for "${bookmarkName}":`, error);
    exists = false;
  }

  return exists;
}

